$mainColor: #2F4F4F;
$personWidth: 230px;

* {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}
.people {
  padding: 30px 55px;
 display: grid;
  gap: 30px 40px;
  grid-template-columns: repeat(2, $personWidth );
  transition: all 1s;
  justify-content: center;
  justify-items: center;
  min-width: 500px;
}

@media (min-width: 1000px) {
  .people {
    grid-template-columns: repeat(4, $personWidth);
  }
}

.person {
  transition: opacity, border-radius  1s;

}

.photo {
  display: flex;
  align-items: center;

  &-image {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    position: relative;
  
  }
}

.rank {
  position:absolute;
  content: '';
  display: block;
  align-items: center;
  font-size: 20px;
  line-height: 32px;
  text-align: center;
  color: $mainColor;

  background-color: #fff;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  font-size: 16px;
  color: #8B0000;
}

.details {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.name {
  display: block;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
}

.extra {
  display: block;
  opacity: 0;
  font-size: 21.6px;
  line-height: 24.84px;

}

.person:hover {
  transform: scale(1.2);
  background-color: #EEEEEE;
  .extra {
    opacity: 1;
  }
  .photo-image {
    border-radius: 20%;     
  }
}